.main {
  display:flex;
  justify-content:center;
}

.box {
  max-width:500px;
  display:flex;
  flex-direction:column;
  align-items:center;
  margin: 50px;

  img {
    width:80%
  }

  h2{}
}

button {
  background:#ffe3b3;
  padding:10px 20px;
  border-radius:8px;
  margin:4px;
  border:1px solid #dbb97d;
  cursor: pointer;

  &:hover {
    background: #dbb97d;
    color:#fff
  }
}

.tie{color:gray;}
.win{border:5px solid red; color:red; border-radius: 20px; background-color: rgb(106, 160, 223)}
.lose{border-radius: 20px; background:fade-out(rgba(0,0,0,0.5),0.4); filter: opacity(30%);}


